

//CSS
import '../CSS/descripcion.css'

//Imagenes
import restaurant1 from '../IMG/restaurantfotos/24941078.jpg'
import restaurant2 from '../IMG/restaurantfotos/24941085.jpg'
import restaurant3 from '../IMG/restaurantfotos/imagenilustrativa.jpg'
import restaurant4 from '../IMG/restaurantfotos/imagenilustrativa2.jpg'

function Descripcion() {
 
        return (
      <>
       
       <section className='cuerpo' > 
        <section className='tit_seccion'>
          Sobre Nosotros
        </section>
        <section className='seccion1' >
            <div className='parrafo_izq'>
            <span className='michef'>MyChef </span> es una cadena de restaurantes ubicados por todo del pais en los cuales nuestro proposito es
            ofrecer comida de calidad como lo hara aun chef y de bajo costo con el fin de lograr que las personas ubicadas
            cercas de nuestros restaurantes tengan la oportunidad de satisfacer su paladar. Entre ms cresca la popularidad 
            de nuestro restaurant en una ciudad y solicitud de pedidos se van a ir incorporando nuevos tipos de comida de 
            acuerdo a encuestas a nuestros clientes de que les  gustara comer en un futuro.
            </div>
            <img className="imgsdescripcionizq"   src={restaurant1}         alt="Foto del restaurant"/>
        </section>
        <section className='seccion2'>
        <img className="imgsdescripcionder"   src={restaurant2}         alt="Foto del restaurant"/>
            <div className='parrafo_der'>
            Cada da estamos creando platillos que pueden interezar a nuestros comensales, as que puedes probar el platillo del da,
            un platillo realizado una unica vez, si nuestros comenzales les agrada en base a encuestas en linea de nuestros clientes 
            registrados y con pago de pedido anticipado podemos repetir dicho platillo. Estamos muy motivados por el apoyo que hemos
            por lor que siempre vamos mejorando da a da. Tambin aceptamoso nuevas propuestas o creamos nuevas ideas que se nos presentan. 
            Nuestra historia va de la mano con la historia primer restaurant que se presentara a continuacin:
            </div>
    
       </section>
       <section className='tit_seccion'>
          Origen de la palabra Restaurante
        </section>
        <section className='seccion3' >
            <div >
            El concepto restaurante proviene del francs restaurant. Se utiliz por primera vez en una taberna que venda sopas y dentro 
            de la frase en latn: Venite ad me vos qui stomacho laboratis et ego restaurabo vos, que el mesonero Boulanger utilizaba a modo 
            de eslogan en el muro de entrada de su negocio y que al castellano podramos traducir como: Venid a m todos los de estmago 
            cansado y yo os lo restaurar. <p></p>
            Tambin en el siglo XVIII refirindose a la comida que se ofreca (un caldo de carne) como restaurativo podemos encontrar el 
            trmino en castellano. El mismo que en francs se utilizar como Restaurant.En cualquier caso, el trmino restaurant se 
            extendi rpidamente por Europa y en el siglo XVIII ya encontramos casas de comidas en Italia, Polonia y Alemania.
            </div>
            <div className='centrar2img'>
            <img className="imgsdescripcion"   src={restaurant3}         alt="imagen ilustrativa de antigua restaurant"/>
            <img className="imgsdescripcion"   src={restaurant4}         alt="imagen ilustrativa de antigua restaurant"/>
            </div>
        </section>
      </section>
      
  
        
      </>
    )
}
export default Descripcion